import React from 'react'
import { FaGraduationCap, FaBriefcase } from 'react-icons/fa'

const Resume = () => {
    return (
        <section id="experience-sec" className="about-sec min-vh-100 ">
            <div className="container">
                <div className="section-title ">
                    <h1 className="sec-h1 fw-bold fs-1">Resume</h1>
                </div>
                <div className="row my-5 ">
                    <div className="col-md-6">
                        <p className="fw-bold">Education</p>
                        <div className="time-line rounded-corner shadow-dark p-5">
                            <div className=" animated d-flex"  data-aos="fade-up">
                                <div style={{ color: '#ff102c' }}>
                                    <FaGraduationCap size={32} />
                                </div>
                                <div className="time-line-container content ms-3">
                                    <span>2019-present</span>
                                    <h4 className="fw-bold">Diploma in CSE</h4>
                                    <p>Currently Pursuing Diploma in feild of computer science and engineering from an Autonomus Institude Government Polytechnic, Amravati.</p>
                                </div>
                            </div>
                            <div className=" animated d-flex mt-3"  data-aos="fade-up">
                                <div style={{ color: '#ff102c' }}>
                                    <FaGraduationCap size={32} />
                                </div>
                                <div className="time-line-container content ms-3">
                                    <span>2009-2019</span>
                                    <h4 className="fw-bold">State Board Edu.</h4>
                                    <p>Completed my Primary and Secondary Education from Hindu Dnyanpeeth Convent, Akola. I got 94% in my 10th Board Exams. Also participated in co-ciricular activities.</p>
                                </div>
                            </div>

                            <span className="line"></span>
                        </div>
                    </div>
                    <div className="col-md-6 mt-4 mt-md-0">
                        <p className="fw-bold">Work</p>
                        <div className="time-line rounded-corner shadow-dark p-5">
                            <div className=" animated d-flex"  data-aos="fade-up">
                                <div style={{ color: 'red' }}>
                                    <FaBriefcase size={32} />
                                </div>
                                <div className="time-line-container content ms-3" data-aos-duration="2000">
                                    <span>2020-present</span>
                                    <h4 className="fw-bold">Web Designing</h4>
                                    <p>I started learning Web Designing in college.Learned the basic web designing tech such as HTML,CSS and JS.Later I also learned React.</p>
                                </div>
                            </div>
                            <div className=" animated d-flex mt-3"  data-aos="fade-up" data-aos-duration="2000">
                                <div style={{ color: 'red' }}>
                                    <FaBriefcase size={32} />
                                </div>
                                <div className="time-line-container content ms-3">
                                    <span>2020-present</span>
                                    <h4 className="fw-bold">Web Development</h4>
                                    <p>Learning Full Stack Web Development Skills. Learning MongoDB, Nodejs, MySQL and Other web development stuff. I can develop decent web apps.</p>
                                </div>
                            </div>
                            <span className="line"></span>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Resume
