import React from 'react'
import ProgressData from '../Data/ProgressData'
import ProgressItem from './elements/ProgressItem'

const Skills = () => {
    return (
        <section id="skills-sec" className="about-sec min-vh-100 ">
            <div className="container" >
                <div className="section-title ">
                    <h1 className="sec-h1 fw-bold fs-1">My Skills</h1>
                </div>
                <div className="container">
                    <div className="row mt-5">
                        {ProgressData.map(pdata=><ProgressItem key={pdata.id} title={pdata.title} progress={pdata.progress} color={pdata.color}/>)}
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Skills
