import React, { useState } from 'react'
import { Link } from 'react-scroll'

const Navbar = () => {

    const [isOpen, setIsOpen] = useState(false);

    const toggle = () => {
        setIsOpen(!isOpen);
    };
    return (
        <header className="fixed-top header">
            <div className="container">
                <nav className="navbar navbar-expand-lg navbar-dark" >
                    <div className="container-fluid">
                        <Link className="navbar-brand fs-3 fw-bold" href="#">AyushB.</Link>
                        <div id="nav-icon1" className={`${isOpen ? 'open' : ''} navbar-toggler`} onClick={toggle} data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                            <span></span>
                            <span></span>
                            <span></span>
                        </div>
                        <div className="collapse navbar-collapse" id="navbarSupportedContent">
                            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                                <li className="nav-item pe-4 fw-bold">
                                    <Link to="home-sec" className="nav-link" aria-current="page"  activeClass="active">Home</Link>
                                </li>
                                <li className="nav-item pe-4 fw-bold">
                                    <Link to="about-sec" className="nav-link" activeClass="active">About</Link>
                                </li>
                                <li className="nav-item pe-4 fw-bold">
                                    <Link to="experience-sec" className="nav-link" activeClass="active">Resume</Link>
                                </li>
                                <li className="nav-item pe-4 fw-bold">
                                    <Link to="services-sec" className="nav-link" activeClass="active">Service</Link>
                                </li>
                                <li className="nav-item pe-4 fw-bold">
                                    <Link to="projects-sec" className="nav-link" activeClass="active">Project</Link>
                                </li>
                                <li className="nav-item pe-4 fw-bold">
                                    <Link to="contact-sec" className="nav-link" activeClass="active">Contact</Link>
                                </li>
                            </ul>
                        </div>
                    </div>
                </nav>

            </div>
        </header>


    )
}

export default Navbar
