import React from 'react'

const ServicesBox = ({ title, img, des,color,fade }) => {
    return (
        <div className="col-md-4" data-aos={`fade-${fade}`}>
            <div
                className="services-box rounded-corner mt-4 d-flex p-4 justify-content-center text-light align-items-center  flex-column text-center"
                style={{
                    backgroundColor: color,
                    boxShadow: color+"70 0px 5px 20px 0px",
                }}
            >
                <img
                    src={img}
                    alt=""
                    className="img-fluid mb-3"
                    style={{ width: "100px", height: "100px" }}
                />
                <h4 className="fw-bold mb-3">{title}</h4>
                <p>{des}</p>
            </div>
        </div>

    )
}

export default ServicesBox
