import React from "react";
import ServicesData from "../Data/ServicesData";
import ServicesBox from "./elements/ServicesBox";

const Services = () => {
  const fade = ['right','up','left'];
  return (
    <section id="services-sec" className="about-sec min-vh-100 ">
      <div className="container">
        <div className="section-title ">
          <h1 className="sec-h1 fw-bold fs-1">Services</h1>
        </div>
        <div className="row mt-5 ">
          {ServicesData.map((data) => (
            <ServicesBox
              key={data.id}
              img={data.img}
              title={data.title}
              des={data.des}
              color={data.color}
              fade={fade[data.id-1]}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default Services;
