import React from "react";

const ProgressItem = ({ title, progress, color }) => {
  return (
    <div className="col-md-6" data-aos="fade-up">
      <div className="skill-item mt-4">
        <div className="skill-info d-flex justify-content-between">
          <p className="m-0 fw-bold">{title}</p>
          <p className="m-0">{progress}%</p>
        </div>
        <div class="progress">
          <div
            class="progress-bar"
            role="progressbar"
            style={{ width: progress + "%",backgroundColor:color }}
            aria-valuenow={progress}
            aria-valuemin="0"
            aria-valuemax="100"
          ></div>
        </div>
      </div>
    </div>
  );
};

export default ProgressItem;
