import React from 'react'
import person from '../images/person2.png'
import { SiInstagram, SiTwitter, SiLinkedin, SiFacebook, SiFiverr } from "react-icons/si";
import patterns from '../patterns'
import Typical from 'react-typical'


const HeroSection = () => {
    return (
        <section id="home-sec">
            <div className="container-fluid d-flex align-items-center justify-contet-center text-light vh-100" >
                <div className="container mt-4 d-flex align-items-center justify-content-center flex-column" data-aos="fade-up">
                    <img src={person} alt="" className="img-fluid mb-3" style={{ height: "130px", width: "130px" }} />
                    <h1 className="text-center fs-1 fw-bold">Ayush Bulbule</h1>
                    <p className=" typed-text text-center m-0 mt-3 w-full">I'm a{' '}<Typical
                        steps={['Web Designer', 3000,
                            'Web Developer', 2000,
                            'Freelancer', 4000]}
                        loop={Infinity}
                        wrapper="p"
                    /></p>

                    <ul className="social-icons list-inline light mb-0 ">
                        <li className="list-inline-item mx-2">
                            <a href="https://instagram.com/ayus_2413" id="ic_insta">
                                <div id="ic_twitter">
                                    <SiInstagram size={22} />
                                </div>
                            </a>
                        </li>
                        <li className="list-inline-item mx-2">
                            <a href="https://instagram.com/ayus_2413" >
                                <div id="ic_twitter">
                                    <SiTwitter size={22} />
                                </div>
                            </a>
                        </li>
                        <li className="list-inline-item mx-2">
                            <a href="https://instagram.com/ayus_2413" id="ic_in">
                                <div id="ic_in">
                                    <SiLinkedin size={22} />
                                </div>
                            </a>
                        </li>
                        <li className="list-inline-item mx-2">
                            <a href="https://instagram.com/ayus_2413">
                                <div id="ic_fi">
                                    <SiFiverr size={22} />
                                </div>
                            </a>
                        </li>
                        <li className="list-inline-item mx-2">
                            <a href="https://instagram.com/ayus_2413" id="ic_fb">
                                <div id="ic_fb">
                                    <SiFacebook size={22} />
                                </div>
                            </a>
                        </li>
                    </ul>

                    <div className="mt-4 mb-5">
                        <button className="btn-hero">Hire Me!</button>
                    </div>

                </div>
                <div className="scroll-down">
                    <a href="" className="scroll-wrap">
                        <span>Scroll Down</span>
                        <span className="mouse">
                            <span className="wheel">
                            </span>
                        </span>

                    </a>
                </div>
                <div className="parallax d-none d-md-block">
                    {patterns.map((p) => p)}
                </div>
            </div>
        </section>
    )
}

export default HeroSection
