import React from 'react'
import Imag1 from '../../images/project1.svg'


const ProjectItem = ({title,des,img}) => {
    return (
        <div className="col-md-4 col-sm-6 p-2" >
            <a href="">
                <div className="project-item shadow-dark rounded-corner" data-aos="fade-up">
                    <div className="details text-center">
                        <h4>{title}</h4>
                        <p>{des}</p>
                    </div>
                    <div className="thumb">
                        <img src={img} alt="" className="img-fluid" />
                    </div>
                </div>
            </a>
        </div>
    )
}

export default ProjectItem
