import React from 'react'
import ProjectItem from './elements/ProjectItem'
import ProjectData from '../Data/ProjectsData'

const Projects = () => {
    return (
        <section id="projects-sec" className="about-sec min-vh-100 ">
            <div className="container">
                <div className="section-title ">
                    <h1 className="sec-h1 fw-bold fs-1">My Projects</h1>
                </div>
                <div className="row my-5 projects-wrapper g-3">
                {
                    ProjectData.map(data=><ProjectItem key={data.id} title={data.title} des={data.skill} img={data.img}/>)
                }
                </div>
            </div>
        </section>
    )
}

export default Projects
